<template>
  <el-timeline>
    <el-timeline-item v-for="(activity, index) in titleList" :key="index" :timestamp="activity.time" placement="top" center>
      <span class="indexTitle" @click="handleIndex(index)">{{ activity.title }}</span>
    </el-timeline-item>
  </el-timeline>
</template>
  
  <script setup>
import { ref, reactive, onMounted } from 'vue'
import { useRouter } from 'vue-router'
let titleList = ref([])
let router = useRouter()
onMounted(() => {
  titleList.value = JSON.parse(localStorage.getItem('blogList'))
  console.log(titleList.value)
})

const handleIndex = index => {
  router.push(`/blog/${index}`)
}
</script>
<style lang="scss">
.indexTitle {
  cursor: pointer;
}
</style>